.front_media {
  display: flex;
  flex: 1 1 auto;
  justify-content: center;
  align-items: center;
  /* width: calc(95% - 0.5em);  */
  height: 300px;
  padding: 0.5em;
  background-color: #eee;
  border-radius: 0.5em;
  object-fit: cover;
  vertical-align: middle;
}

.front_media img {
  max-width: none;
  min-width: auto;
  min-height: auto;
  padding: 1.5em;
  /* width: calc(95% - -7.5em); */
  height: 117%;
  /* min-width: 200px; */
  width: auto;
}

.front_body {
  display: flex;
  justify-content: center;
  align-items: center;
}

