  vertical-align: middle;
  flex-direction: column;
  overflow-y: scroll;
  flex-wrap: wrap;
  flex-direction: row-reverse;
}

.mediaSong::-webkit-scrollbar {
  width: 0.1px;
}

.mediaImage {
  max-width: none;
  min-width: auto;
  min-height: auto;
  padding: 1.5rem;
  height: 60%;
  width: auto;
}

.mediaImage>img {
  display: block;
  width: 100%;
  height: 250px;
  object-fit: cover;
}

.headerSong {
  text-align: right;
  font-size: 1.5rem;
  padding: 0.2rem 1.3rem;
  color: var(--titleSongColor);
  background-color: var(--colorBackgroundAll);
  border-radius: 0.5rem;
  border: 2px solid var(--titleSongColor);
  border-bottom: 4px solid #b2b1b1;
  text-shadow: -1px -1.6px 0px #9440f3;
  box-shadow: var(--boxShadow);
  cursor: pointer;
}

.linkTo {
  position: absolute;
  top: 96%;
  left: 31%;
  background-color: var(--colorText);
  border: none;
  color: white;
  padding: 2px 6px;
  font-size: 14px;
  cursor: pointer;
  border-radius: .5em;
  z-index: 7;
}


.audioBlock {
  display: block;

}

.audioBlock>p {
  font-size: 2.5rem;
  text-align: center;
  color: var(--colorText);
  /* padding: 2px 6px; */
  background-color: var(--tributeAfterBefore);
  border-radius: 0.5em;
  text-shadow: var(--textShadow);
  box-shadow: var(--boxShadow);
}

.videoBlock>p {
  display: block;
  font-size: 2.5rem;
  text-align: center;
  color: var(--colorText);
  background-color: var(--tributeAfterBefore);
  border-radius: 0.5em;
  text-shadow: var(--textShadow);
  box-shadow: var(--boxShadow);
}

.videoBlock {
  position: relative;

  width: 100%;
  height: 255px;

}

#videoFrame {
  /* position: absolute; */
  top: 0;
  left: 0;
  width: 100% !important;
  height: 22vh !important;
  border-width: 0;
  outline-width: 0;
}

audio::-webkit-media-controls-enclosure {

  background-color: var(--titleSongColor);
  text-shadow: -1px -1.6px 0px #9440f3;
  box-shadow: var(--boxShadow);

}

.tziTata {
  position: relative;
  padding-bottom: 20px; /* по размеру инпута */
  /* height: 76rem; */
}

.tziImage {
  height: auto;
  width: 100%;
  
}